/**
 * 时间转时间戳
 * @param {*} str
 */


export function timeStampToString(str, format = "Y-m-d H:i:s") {
    format = format.toLowerCase();
    
    let formatArr = ["y", "m", "d", "h", "i", "s"];
    
    let timeData = format;
    
    let dateNum = new Date(str * 1000);
    
    formatArr.forEach((item) => {
        
        if (format.search(item) != -1) {
            
            switch (item) {
                
                case "y": {
                    timeData = timeData.replace(eval("/" + item + "/g"), dateNum.getFullYear())
                    break;
                }
                
                case "m": {
                    let MM = dateNum.getMonth() + 1;
                    MM = MM < 10 ? ('0' + MM) : MM;//月补0
                    timeData = timeData.replace(eval("/" + item + "/g"), MM)
                    break;
                }
                
                case "d": {
                    let d = dateNum.getDate();
                    d = d < 10 ? ('0' + d) : d;//天补0
                    timeData = timeData.replace(eval("/" + item + "/g"), d)
                    break;
                }
                
                case "h": {
                    let h = dateNum.getHours();
                    h = h < 10 ? ('0' + h) : h;//小时补0
                    timeData = timeData.replace(eval("/" + item + "/g"), h)
                    break;
                }
                
                case "i": {
                    let m = dateNum.getMinutes();
                    m = m < 10 ? ('0' + m) : m;//分钟补0
                    timeData = timeData.replace(eval("/" + item + "/g"), m)
                    break;
                }
                
                case "s": {
                    let s = dateNum.getSeconds();
                    s = s < 10 ? ('0' + s) : s;//秒补0
                    
                    timeData = timeData.replace(eval("/" + item + "/g"), s)
                    break;
                }
                
                default:
            }
            
        }
    })
    
    return timeData;
}

/**
 * 时间转时间戳
 * @param {*} str
 */


export function getDateDiff(dateTimeStamp) {
    var minute = 1000 * 60;
    var hour = minute * 60;
    var day = hour * 24;
    var month = day * 30;
    var year = month * 12;
    
    var now = new Date().getTime();
    var diffValue = now - dateTimeStamp * 1000;
    
    
    let result = ''
    var yearC = parseInt(diffValue / year);
    var monthC = parseInt(diffValue / month);
    var weekC = parseInt(diffValue / (7 * day));
    var dayC = parseInt(diffValue / day);
    var hourC = parseInt(diffValue / hour);
    var minC = parseInt(diffValue / minute);
    if (yearC >= 1) {
        result = yearC + "年之前";
    } else if (monthC >= 1) {
        result = monthC + "个月之前";
    } else if (weekC >= 1) {
        result = weekC + "周之前";
    } else if (dayC >= 1) {
        result = dayC + "天之前";
    } else if (hourC >= 1) {
        result = hourC + "个小时前";
    } else if (minC >= 10) {
        result = minC + "分钟前";
    } else
        result = "刚刚发表";
    return result;
}


/**
 * @description 将图片的base64 转变成Blob形式
 * */

export function getImageByCanvas(dataurl) {
    var arr = dataurl.split(','), mime = arr[0].match(/:(.*?);/)[1],
            bstr = atob(arr[1]), n = bstr.length, u8arr = new Uint8Array(n);
    while (n--) {
        u8arr[n] = bstr.charCodeAt(n);
    }
    return new Blob([u8arr], {type: mime});
}

/**
 * @description 判断平台以及是否为Safari浏览器
 * */

export function getPlatform() {
    
    let native = {};
    let p = navigator.platform;
    let u = navigator.userAgent;
    let is_android = u.indexOf('Android') > -1 || u.indexOf('Linux') > -1;
    let is_ios = !!u.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/);
    if (p == "Win32" || p == "Win64" || p == "MacPPC" || p == "MacIntel" || p == "X11" || p == "Linux i686") {//PC调试环境
        native.platform = "pc"
    } else {
        if (is_android) {//Android终端
            native.platform = "android"
        } else if (is_ios) {//IOS终端
            native.platform = "ios"
        }
    }
    var ua = u.toLowerCase();
    if (ua.indexOf('applewebkit') > -1 && ua.indexOf('mobile') > -1 && ua.indexOf('safari') > -1 &&
            ua.indexOf('linux') === -1 && ua.indexOf('android') === -1 && ua.indexOf('chrome') === -1 &&
            ua.indexOf('ios') === -1 && ua.indexOf('browser') === -1) {
        
        native.isSafari = true
    } else {
        native.isSafari = false
    }
    
    //判断是否为微信中打开:
    if (ua.indexOf('micromessenger') !== -1 || ua.indexOf('weibo') !== -1) {
        native.isWeixin = true
    } else {
        native.isWeixin = false
    }
    
    //判断是否为QQ中打开:
    if ((ua.indexOf('iphone') > -1 || ua.indexOf('mac') > -1) && ua.indexOf('qq') > -1) {
        native.isQQ = true
    } else {
        native.isQQ = false
    }
    
    return native
}

/**
 * 隐藏中间几位数
 * @param {*}
 */
export function hideLocation(str, start = 4, num = 4) {
    return str.substring(0, start - 1) + "****" + str.substr(str.length - num);
}

/**
 * @description 数学
 * @param {*}
 */
export function MathRound(float, num) {
    return Math.round(float * Math.pow(10, num)) / Math.pow(10, num)
}

/**
 * @description 去空格
 * @param {*}
 */
export function trim(value) {
    return value.replace(/(^\s*)|(\s*$)/g, "");
}

/**
 * @description 内容替换
 * @param {*}
 */
export function replaceAll(text, repstr, newstr) {
    return text.replace(new RegExp(repstr, "gm"), newstr);
}

