/**
 * @description rgbToHex
 * @param {*}
 */
export function rgbToHex(r, g, b) {
    return "#" + toHex(r) + toHex(g) + toHex(b)
}

/**
 * @description toHex
 * @param {*}
 */
export function toHex(n) {
    n = parseInt(n, 10);
    if (isNaN(n)) return "00";
    n = Math.max(0, Math.min(n, 255));
    return "0123456789ABCDEF".charAt((n - n % 16) / 16) +
            "0123456789ABCDEF".charAt(n % 16);
}

/**
 * @description hexToRgb
 * @param {*}
 */
export function hexToRgb(hex) {
    let result = /^#?([a-f\d]{2})([a-f\d]{2})([a-f\d]{2})$/i.exec(hex);
    return result ? {
        r: parseInt(result[1], 16),
        g: parseInt(result[2], 16),
        b: parseInt(result[3], 16)
    } : null;
}

/**
 * @description 复制函数
 * @param {*}
 */
export function copy(text) {
    var Input = document.createElement('input'); //创建一个隐藏input（重要！）
    Input.value = text; //赋值
    document.body.appendChild(Input);
    Input.select(); // 选择对象
    document.execCommand('Copy'); // 执行浏览器复制命令
    Input.className = 'oInput';
    Input.style.display = 'none';
    this.$message({message: '复制成功', type: 'success'});
}

export function imgonload(imgfile) {
    return new Promise(((resolve, reject) => {
        var img = new Image()
        img.src = imgfile
        img.onload = () => resolve(img)
        img.onerror = reject;
    }))
}

/**
 * 文字背景图合成图片
 * */
export async function establishstrImage(text, imgfile, x = 0, y = 0, color = '#000000', size = '36', font = 'Microsoft YaHei') {
    // 生成图片
    var canvas = document.createElement("canvas");
    let base64img = ''
    await imgonload(imgfile).then((img) => {
        canvas.width = img.width
        canvas.height = img.height
        var cxt = canvas.getContext("2d")
        // 画图(这里画布与图片等宽高)
        cxt.drawImage(img, 0, 0)
        // 设置字体大小
        cxt.font = size + "px " + font
        // 更改字号后，必须重置对齐方式，否则居中麻烦。设置文本的垂直对齐方式
        cxt.textBaseline = 'middle'
        cxt.textAlign = 'left'
        // 距离左边的位置
        var left = x
        // 距离上边的位置 (图片高-文字距离图片底部的距离)
        var top = y
        // 文字颜色
        cxt.fillStyle = color
        // 文字在画布的位置
        cxt.fillText(text, left, top)
        base64img = canvas.toDataURL("image/png")
    })
    if (base64img) {
        return Promise.resolve(base64img)
    } else {
        return Promise.reject()
    }
}

/**
 * HTML反转译
 * */
export function HTMLDecode(text) {
    
    var temp = document.createElement("div");
    
    temp.innerHTML = text;
    
    var output = temp.innerText || temp.textContent;
    
    temp = null;
    
    return output;
    
}

/**
 * HTML转译
 * */
export function HTMLEncode(html) {
    
    var temp = document.createElement("div");
    
    (temp.textContent != null) ? (temp.textContent = html) : (temp.innerText = html);
    
    var output = temp.innerHTML;
    
    temp = null;
    
    return output;
    
}

/**
 * 判断是否为URL
 * */
export function isUrl(str) {
    var v = new RegExp('^(?!mailto:)(?:(?:http|https|ftp)://|//)(?:\\S+(?::\\S*)?@)?(?:(?:(?:[1-9]\\d?|1\\d\\d|2[01]\\d|22[0-3])(?:\\.(?:1?\\d{1,2}|2[0-4]\\d|25[0-5])){2}(?:\\.(?:[0-9]\\d?|1\\d\\d|2[0-4]\\d|25[0-4]))|(?:(?:[a-z\\u00a1-\\uffff0-9]+-?)*[a-z\\u00a1-\\uffff0-9]+)(?:\\.(?:[a-z\\u00a1-\\uffff0-9]+-?)*[a-z\\u00a1-\\uffff0-9]+)*(?:\\.(?:[a-z\\u00a1-\\uffff]{2,})))|localhost)(?::\\d{2,5})?(?:(/|\\?|#)[^\\s]*)?$', 'i');
    return v.test(str);
}