import {codeCateList} from "@/api/codeProducts"
import {CATE} from "@/store/modules/mutations_type";

const codeProduct = {
    state: {
        cate: localStorage.getItem('cate') ? JSON.parse(localStorage.getItem('cate')) :  {}
    },
    
    mutations: {
        [CATE](state, payload) {
            let cate = []
            if (Object.keys(payload)) Object.keys(payload).forEach(item =>{
                let z_item = payload[item]
                cate.push({
                    "title":z_item.title,
                    "pathName":z_item.id
                })
            })
            state.cate = cate
            localStorage.setItem('cate',JSON.stringify(cate))
        }
    },
    
    actions: {
        getCodeCateList({commit}, data) {
            return new Promise((resolve, reject) => {
                codeCateList(data).then((res) => {
                    commit(CATE, res)
                    resolve(res.data)
                }).catch(err => {
                    reject(err)
                })
                resolve()
                reject()
            })
        }
    }
}

export default codeProduct
