import {
    LANG,
    SETLOGO,
    SERVICECONFIG
} from './mutations_type'
import {establishstrImage, isUrl} from "@/utils/other/commonFun"
import {
    isNum
} from '../../utils/other/validate'
import Vue from 'vue'
import {getConfig} from "@/api/service";

const user = {
    state: {
        lang: localStorage.getItem('lang') || "zh-CN",
        account: localStorage.getItem('account') || {},
        logo: localStorage.getItem('logo') || '',
        serviceConfig: localStorage.getItem('serviceConfig') ? JSON.parse(localStorage.getItem('serviceConfig')) :  {}
    },
    
    mutations: {
        [SERVICECONFIG](state, payload) {

            Vue.set(state, 'serviceConfig', payload)
            localStorage.setItem('serviceConfig', JSON.stringify(payload))

        },
        [LANG](state, payload) {
            Vue.set(state, 'lang', payload)
            Vue.set(Vue.prototype._i18n, 'locale', payload)
            localStorage.setItem('lang', payload)
        },
        [SETLOGO](state) {
            if (!state.logo) {
                let logo = require("@/common/images/png/logoBack.png")
                establishstrImage(location.host, logo, 30, 155, "#6b0000b0", 36).then(res => {
                    state.logo = res
                    localStorage.setItem('logo', state.logo)
                })
            }
        }
    },
    getters: {
        contact: state => {//通过方法访问
            let contact = []
            if(Object.prototype.hasOwnProperty.call(state.serviceConfig,'contact')){
                let con = state.serviceConfig.contact
                Object.keys(con).forEach(item => {
                    let c_item = {}
                    c_item.content = con[item]
                    c_item.icon = item
                    c_item.isUrl = isUrl(con[item]) ? true : false
    
                    contact.push(c_item)
                })
            }
            return contact
        },
        webconfig: state => {//通过方法访问
            let contact = []
            if(Object.prototype.hasOwnProperty.call(state.serviceConfig,'web')){
                contact = state.serviceConfig.web
            }
            return contact
        }
    },
    actions: {
        setLang({commit}, num) {
            let _i18n = Vue.prototype._i18n.messages
            if (isNum(num)) {
                let lang_keys = Object.keys(Vue.prototype._i18n.messages);
                
                if (Object.prototype.hasOwnProperty.call(lang_keys, num)) {
                    commit(LANG, lang_keys[num])
                }
            } else {
                if (Object.prototype.hasOwnProperty.call(_i18n, num)) {
                    commit(LANG, num)
                }
            }
            return true;
        },
        get_config({commit}) {
            getConfig().then(res=>{
                commit(SERVICECONFIG,res)
            })
        }
        
    }
}

export default user
