import Vue from 'vue'
import App from './App.vue'
import store from './store'
import router from './router/permission'
import 'element-ui/lib/theme-chalk/index.css';
import ElementUI from 'element-ui';
import {Message} from 'element-ui';
import VueI18n from 'vue-i18n'
import zh from "./common/lang/zh";
import en from "./common/lang/en";
import {establishstrImage} from "@/utils/other/commonFun"
import logo from "@/common/images/png/logoBack.png";

Vue.use(VueI18n) ;
Vue.use(ElementUI);


window.eventBus = new Vue()

establishstrImage(location.host, logo, 30, 170).then(res => {
	window.logoFile  = res
})

const i18n = new VueI18n({
	locale: store.state.config.lang,    // 语言标识, 通过切换locale的值来实现语言切换,this.$i18n.locale
	messages: {
		'zh-CN': zh,   // 中文语言包
		'en-US': en    // 英文语言包
	}
})

const message = (message, type = 4) => {
	let msgType;
	switch (type) {
		case 1:
			msgType = 'success'
			break;
		case 2:
			msgType = 'warning'
			break;
		case 3:
			msgType = 'info'
			break;
		default:
			msgType = 'error'
	}
	Message({
		message: message,
		type: msgType,
		center: true,
		duration: 2000,
		offset:46
	})
}
Vue.config.productionTip = false
Vue.prototype._i18n = i18n
Vue.prototype.$message = message
new Vue({
	store,
	i18n,
	router,
	render: h => h(App)
}).$mount('#app')
