/**
 * 邮箱
 * @param {*} s
 */
export function isEmail(s) {
    return /^([a-zA-Z0-9_-])+@([a-zA-Z0-9_-])+((.[a-zA-Z0-9_-]{2,3}){1,2})$/.test(s)
}

/**
 * 中英文
 * @param {*} s
 */
export function isChsAlphaNum(s) {
    return /^[\u4e00-\u9fa5_a-zA-Z0-9]{4,32}$/.test(s)
}

/**
 * 数字
 * @param {*} s
 */
export function isNum(s) {
    return /^[0-9]{1,13}$/.test(s)
}

/**
 * 路径
 * @param {*} s
 */
// export function isPath(s) {
//     return /^[a-zA-Z0-9\/\.]+$/.test(s)
// }

/**
 * 微信
 * @param {*} s
 */
export function isWechat(s) {
    return /^[a-zA-Z0-9_-]+$/.test(s)
}

/**
 * 验证码
 * @param {*} s
 */
export function isVerify(s) {
    return /^[a-zA-Z0-9]{4,6}$/.test(s)
}

/**
 * 是否重复
 * @param {*} s
 */
export function confirm(a, b) {
    if (a == b) {
        return true;
    }
    return false
}

/**
 * 手机号码
 * @param {*} s
 */
export function isMobile(s) {
    return /^[1][2-9][0-9]{9}$/.test(s)
}

/**
 * 密码
 * @param {Object} s
 */
export function isPassword(s) {
    return /^(?![0-9]+$)(?![a-zA-Z]+$)[0-9A-Za-z]{8,18}$/.test(s)
}

/**
 * 中文姓名
 * @param {Object} s
 */
// export function isRealname(s) {
//     return /^[\u4E00-\u9FA5\.]{2,30}$/.test(s)
// }

/**
 * 支付密码
 * @param {Object} s
 */
export function isPayPassword(s) {
    return /^\d{6}$/.test(s)
}

/**
 * 电话号码
 * @param {*} s
 */
export function isPhone(s) {
    return /^([0-9]{3,4}-)?[0-9]{7,8}$/.test(s)
}

/**
 * URL地址
 * @param {*} s
 */
export function isURL(s) {
    return /^http[s]?:\/\/.*/.test(s)
}

/**
 * 身份证
 * @param {*} s
 */
export function isIdcard(code) {
    var city = {
        11: "北京",
        12: "天津",
        13: "河北",
        14: "山西",
        15: "内蒙古",
        21: "辽宁",
        22: "吉林",
        23: "黑龙江 ",
        31: "上海",
        32: "江苏",
        33: "浙江",
        34: "安徽",
        35: "福建",
        36: "江西",
        37: "山东",
        41: "河南",
        42: "湖北 ",
        43: "湖南",
        44: "广东",
        45: "广西",
        46: "海南",
        50: "重庆",
        51: "四川",
        52: "贵州",
        53: "云南",
        54: "西藏 ",
        61: "陕西",
        62: "甘肃",
        63: "青海",
        64: "宁夏",
        65: "新疆",
        71: "台湾",
        81: "香港",
        82: "澳门",
        91: "国外"
    };
    // eslint-disable-next-line no-unused-vars
    var tip = "";
    var pass = true;
    if (!code || !/^\d{6}(18|19|20)?\d{2}(0[1-9]|1[012])(0[1-9]|[12]\d|3[01])\d{3}(\d|X)$/i.test(code)) {
        tip = "身份证号格式错误";
        pass = false;
    } else if (!city[code.substr(0, 2)]) {
        tip = "地址编码错误";
        pass = false;
    } else {
        if (code.length == 18) {
            code = code.split('');
            var factor = [7, 9, 10, 5, 8, 4, 2, 1, 6, 3, 7, 9, 10, 5, 8, 4, 2];
            var parity = [1, 0, 'X', 9, 8, 7, 6, 5, 4, 3, 2];
            var sum = 0;
            var ai = 0;
            var wi = 0;
            for (var i = 0; i < 17; i++) {
                ai = code[i];
                wi = factor[i];
                sum += ai * wi;
            }
            // eslint-disable-next-line no-unused-vars
            var last = parity[sum % 11];
            if (parity[sum % 11] != code[17]) {
                tip = "校验位错误";
                pass = false;
            }
        }
    }
    return pass;
}

/**
 * 是否是图片类型
 * @param {*} type
 */
export function isImg(type) {
    if (type !== 'image/jpg' && type !== 'image/jpeg' && type !== 'image/png' && type !== 'image/gif') {
        this.$message.error('只支持jpg、png、gif格式的图片！')
        return false
    }
    return true
}

/**
 * 是否是视频类型['video/mp4', 'video/ogg', 'video/flv','video/avi','video/wmv','video/rmvb']
 * @param {*} type
 */
export function isVideo(type) {
    if (type !== 'video/mp4' && type !== 'video/ogg' && type !== 'video/flv' && type !== 'video/avi' && type !== 'video/wmv' && type !== 'video/rmvb') {
        this.$message.error('只支持mp4、ogg、flv、avi、wmv、rmvb格式的视频！')
        return false
    }
    return true
}

/**
 * 是否是压缩包类型
 * @param {*} type
 */
export function isZip(type) {
    if (type !== 'application/zip') {
        this.$message.error('只支持zip格式的图片！')
        return false
    }
    return true
}

/**
 * 是否是文档类型['application/msword'、'application/pdf']
 * @param {*} type
 */
export function isDoc(type) {
    if (type !== 'application/zip' && type !== 'application/pdf') {
        this.$message.error('只支持doc、docx、pdf格式的文档！')
        return false
    }
    return true
}